function initContract () {
  if (typeof window.web3 !== 'undefined') {
    window.web3.version.getNetwork((er, netId) => {
      let eth_
      if (typeof netId !== 'undefined') {
        store.commit('setNetwork', netId)
        if (netId === '1') { // 
          store.commit('setNetworkShort', 'Main Net')
        } else {
          store.commit('setNetworkShort', 'Test Net')
        }

        //  
        if (netId === store.getters.getPresetNetwork) {
          console.log('network is right:' + netId)
          web3 = new Web3(window.web3.currentProvider)
          eth_ = new Eth(window.web3.currentProvider)
        } else {
          console.log('network is wrong: ' + netId)
          web3 = new Web3(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))
          eth_ = new Eth(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))
        }
      } else {
        console.log('network is undefined')
        web3 = new Web3(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))
        eth_ = new Eth(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))
      }

      let ethContract = new EthContract(eth_)
      // seashells = ethContract(abi).at(address)
      seashells = ethContract(abi).at(address)

      store.commit('setContractAddress', address)
      console.log('contractAddress' + store.getters.getContractAddress)

      getSsMeta()
      setTimeout(initMetadata(), 1000)
      // 
      checkExpress()
    })
  } else { // metamask infura 
    console.log('not metamask & connect: ' + store.getters.getMustNetworkHttp)
    web3 = new Web3(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))
    let eth = new Eth(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))

    let ethContract = new EthContract(eth)
    seashells = ethContract(abi).at(address)

    store.commit('setContractAddress', address)
    console.log('contractAddress' + store.getters.getContractAddress)

    getSsMeta()
    setTimeout(initMetadata(), 1000)
    // 
    checkExpress()
  }

  return seashells
}

