// 
function getSsMeta () {
  seashells.name.call()
    .then((result) => {
      let rest = result[0].toString()
      console.log('getName: ' + rest)
      store.commit('setTotalName', rest)
    })

  seashells.symbol.call()
    .then((result) => {
      let rest = result[0].toString()
      store.commit('setTokenSymbol', rest)
    })

  seashells.onlyAmbassadors.call()
    .then((result) => {
      let rest = result[0].toString()
      store.commit('setOnlyAmbassadors', rest)
    })
}

// 
function initMetadata () {
  totalSupply()
  totalEthereumBalance()
  buyPrice()
  sellPrice()

  // 
  if (typeof window.web3 !== 'undefined') {
    // 

    if (window.web3.eth.coinbase !== null && store.getters.getNetwork === store.getters.getPresetNetwork) {
      let myAddress = window.web3.eth.coinbase
      store.commit('setCustomerAddress', myAddress)

      // 
      myTokens(myAddress)
      myDividends(1, myAddress)
    }
  }
  // 10,setInterval
  setTimeout(function () {
    if (seashells !== null) {
      initMetadata()
    }
  }, 1000 * 15)
}

