function isAmbassadors (_address) {
  let ambassadors = [
    '0xBe1bB799f587249ad8bEf492Cc48E0D5bBB2c991',
    '0x379F025b37E04d9d5ec4e3198b36717FA54C513c',
    '0x2E29304769e5fdD0bF4F6Eb3174afA6a08B5aC75',
    '0x5cB8629504F3F79b0E2c935e68e6976B5239a9c1',
    '0xe190c28bb3965C7013A523848181175228055e8f',
    '0x76637b31163b438b15BBf61Bcf878495F7A0Ea4f',
    '0x2d6283FF3777Ae609B01b4BB9dA1e95088204400',
    '0x36cA5db7bd28ad839a9BD2Ab79dF6F67bF7DfB2F',
    '0xCf52875fda3ad1994886651Dd4D8ADc2938bB245',
    '0xBF9A45f956F459D470235f4B25f6C44d9f501ad3',
    '0x026283a2a9e0cd723425645F1cE18168d02DDD01',
    '0xdcC5D774a54733981E94C245172F7E2Cd8749771',
    '0x5025fDB82e3813B2077a944FEaA166553eCe9302',
    '0x88438a476AdE52368E518A741EC13ED27E8C14f6',
    '0xfaCF4cF9Be08fFfBA03ab698A1c28f4818d5Bf5c',
    '0x569afbB4AA0ED54C1E1b4a1958f0FBc0eBf60bb1',
    '0xF74493fa653AC573da925E50388eb6368aC41A9E',
    '0xFF6fa8D19b9534104c0F4F81C82dFb0f30c1Cb3c',
    '0x1f187B81fB7b0168a0261380c93C4C370F5ABb06',
    '0x2B2f84E55b21a743Ed1d37f0F574eF817663d362',
    '0x706e17Ab71f84C704945Bd272f3B34FC8b32cE09',
    '0x9b55B24Ae2f16CD509D1e57a89EB8985CA249Cb0',
    '0x1151A85fA688cf21949a277242A610f4d8d1fAD0',
    '0xe65d09a935aC95377ECa215a3Fc7827bCD69b5cb'
  ]

  let checksumAddress = web3.toChecksumAddress(_address)
  for (let ambassador in ambassadors) {
    if (web3.toChecksumAddress(ambassadors[ambassador]) === checksumAddress) {
      return true
    }
  }
  return false
}

